// import libraries
import * as React from "react";

export default function About() {
  return (
    <div className="h-screen min-h-max overflow-y-auto flex justify-center content-center items-center">
      <div className="flex flex-col gap-4">
        <h1 className="text-center text-2xl font-title" style={{color: '#1976d2'}}>
          {process.env.REACT_APP_NAME ? process.env.REACT_APP_NAME : "Sistem Informasi Pemetaan Wisata Menggunakan Metode Heversine Formula"}
        </h1>
        <p className="text-center text-gray-500">
          {process.env.REACT_APP_VERSION ? process.env.REACT_APP_VERSION : "Versi 1.0"}
        </p>
      </div>
    </div>
  );
}
